import classifyData from './classify.data.js';
import { mapGetters } from "vuex"
export default {
	data() {
		return {
			name: "wkiwi",
			height: 0,
			categoryActive: 0,
			scrollTop: 0,
			scrollLeftTop: 0,
			// scrollHeight: 0,
			classifyData:[],
			arr:[0,584,1168,1752,2336,2805,3274,3858,4442,4911,5380,5734,6203,6672,7017],//初始值，后边计算会根据手机适配覆盖
			leftItemHeight: 51,//49行会计算出新值进行覆盖
			navLeftHeight:0,//左边scroll-view 内层nav的总高度
			diff: 0,//左边scroll-view 内层nav的总高度与视口之差
			tabBarHeight:51,//如果此页面为Tab页面，自己改变高度值,,一般tab高度为51
			cate:[]
		}
	},
	created(){
		//如果你的分类数据为后台异步获取请	将下方代码放置你的数据回调中
		// this.$nextTick(()=>{
		// 	this.getHeightList();
		// })
	},
	onLoad: function () {
		this.getData()
		this.height = uni.getSystemInfoSync().windowHeight - this.tabBarHeight;
		console.log(uni.getSystemInfoSync())
	},
	onReady() {
		
	},
	computed:{
		...mapGetters(['getGoods'])
	},
	onShow() {
		// this.$update(this.getGoods)
	},
	onHide() {
		this.$view.hide()
	},
	methods: {
		// 获取滚动数据
		getData(){
			this.$R.get("/goods/indexcategory/").then(res=>{
				res.forEach(v=>{
					this.cate.push({
						id:v.id,
						name:v.name
					})
				})
				this.resLength = res.length
				for(let i=0;i<res.length;i++){
					this.geList(i)
				}
			})
		},
		geList(index){
			this.$R.get("/goods/goodslist/",{id:this.cate[index].id}).then(re=>{
				var ites = re.results.map(y=>{
					// {
					//   "name": "A字裙",
					//   "key": "A字裙",
					//   "icon": "http://img.kiwifruits.cn/classify/1/1.jpg",
					//   "cat": 10
					// }
					return{
						icon:y.goods_front_image,
						name:y.name,
						key:y.name,
						id:y.id
					}
				})
				this.classifyData.push({
					foods:ites,
					name:this.cate[index].name
				})
				this.getHeightList();
			})
		},
		getHeightList(){
			let _this = this;
			let selectorQuery=uni.createSelectorQuery();
			selectorQuery.selectAll('.nav-left-item').boundingClientRect(function(rects) {
				_this.leftItemHeight  =  rects[0].height;
				_this.navLeftHeight = _this.leftItemHeight * classifyData.length;
				_this.diff =  _this.navLeftHeight - _this.height;
			});
			selectorQuery.selectAll('.box').boundingClientRect(function(rects) {
				let arr = [0];
				let top = 0;
				rects.forEach(function(rect){
// 					rect.id      // 节点的ID
// 					rect.dataset // 节点的dataset
// 					rect.left    // 节点的左边界坐标
// 					rect.right   // 节点的右边界坐标
// 					rect.top     // 节点的上边界坐标
// 					rect.bottom  // 节点的下边界坐标
// 					rect.width   // 节点的宽度
// 					rect.height  // 节点的高度
					top += rect.height;
					arr.push(top)
					})
					_this.arr = arr
				}).exec()
		},
		scroll(e) {
			let _this = this
			if(this.timeoutId){
				clearTimeout(this.timeoutId);
			}
			this.timeoutId = setTimeout(function(){ //节流
				_this.scrollHeight = e.detail.scrollTop + 1 + _this.height/3;
				//+1不要删除，解决最后一项某种情况下翻到底部，左边按钮并不会切换至最后一个
				//若想使切换参考线为屏幕顶部请删除 _this.height/2
				for (let i = 0; i < _this.arr.length;i++) {
					let height1 = _this.arr[i];
					let height2 = _this.arr[i+1];
					if (!height2 || (_this.scrollHeight >= height1 && _this.scrollHeight < height2)) {
						_this.categoryActive = i;
						(_this.diff>0) && (_this.scrollLeftTop = Math.round((_this.categoryActive * _this.diff)/(classifyData.length-1)));
						return false;
					}
				}
				_this.categoryActive = 0;
				_this.timeoutId = undefined;
			}, 10)
		},
		categoryClickMain(index) {
			this.categoryActive = index;
			this.scrollTop == this.arr[index] ? this.scrollTop = this.scrollTop+1 : this.scrollTop = this.arr[index]//防止两次相等造成点击不触发滚动时间
		},
		cart: function (id) {
			uni.navigateTo({
				url:"../detail/detail?detail="+id,
				 animationType: 'pop-in',
				 animationDuration: 200
			})
		}
	},
	components: {
	}
}
